// EB Garamond

@font-face {
    font-family: "EB Garamond";
    src: url("/fonts/ebg-rnd.woff2") format('woff2');
    font-display: swap;
}

@font-face {
    font-family: "EB Garamond";
    src: url("/fonts/ebg-rnd.woff2") format('woff2');
    font-weight: bold;
    font-display: swap;
}

@font-face {
    font-family: "EB Garamond";
    src: url("/fonts/ebg-ita.woff2") format('woff2');
    font-style: italic;
    font-display: swap;
}

@font-face {
    font-family: "EB Garamond";
    src: url("/fonts/ebg-ita.woff2") format('woff2');
    font-weight: bold;
    font-style: italic;
    font-display: swap;
}

// Karla

@font-face {
    font-family: "Karla";
    src: url("/fonts/karla.woff2") format('woff2');
    font-display: swap;
}

@font-face {
    font-family: "Karla";
    src: url("/fonts/karla.woff2") format('woff2');
    font-weight: bold;
