html {
    font-size: $font_size;
}

body {
    background-color: $background_color;
    color: $text_color;
    font-family: $base_fonts, $emoji_fonts;
    line-height: 1.5;
    display: flex;
    flex-direction: column;
    // margin: 0;
}

a {
    color: $link;

    &:visited {
        color: $visited_link;
    }

    &:hover {
        color: $hover_link;
    }
}

h1, h2 {
    font-size: 1rem;
    font-weight: bold;
    margin-top: 2rem;
}

sup {
    line-height: 0;
    position: relative;
}

.sezione {
    display: flex;
    flex-direction: row;
}

.intestazioni-liste {
    height: 1.4rem;

    .righe {
        fill: rgb(226, 64, 0);
    }
}

#racconti {
    list-style: url(/img/asterisco.svg);

    li {
        padding-left: .4rem;
    }
}

#blog {
    list-style: none;

    .date {
        margin-right: 1rem;
        font-variant-numeric: tabular-nums;
    }
}

#chi-sono {
    margin: 4rem 0;

    #bio {
        font-size: 1.4rem;

        em {
            font-style: italic;
        }
    }

    .pulsante {
        display: block;
        margin: 2rem auto;
        width: 10rem;

        svg #background-splash {
            fill: #ffd500;
            opacity: 0;
            transition: .2s;
        }

        &:hover svg #background-splash {
            opacity: 1;
        }
    }
}