article {
    margin: 2rem 0;

    p {
        -webkit-hyphens: auto;
        hyphens: auto;
    }

    figure {
        max-width: 20rem;
        margin: 2rem auto;
        text-align: center;
        // border: 1px solid grey;
        border-radius: .2rem;
        padding: 1rem;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;

        img {
            width: 100%;
            border-radius: 2px;
        }

        figcaption h4 {
            font-weight: 400;
            font-size: .8rem;
            font-family: $mono_fonts;
            margin: 0.7rem 0 0 0;
        }
    }

    aside {
        margin: 2rem 0;
        font-size: .8rem;
        color: $slightly_dimmed_text;
    }

    &.post {

        &.micro header .byline {
            display: none;
        }

        .footnotes {
            font-size: .8rem;
        }

        #post-nav {
            margin-top: 1rem;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            width: 100%;

            .date {
                // font-style: italic;
                // font-family: $mono_fonts;
                font-size: .6rem;

                &::before {
                    content: "(";
                }

                &::after {
                    content: ")";
                }
            }

            .next {
                justify-content: end;
            }

            .previous {
                justify-self: start;
            }

            p {
                margin: 0;
            }
        }
    }

    &.page {

        h2 {
            margin-left: -2rem;
    
            &::before {
                background-image: url(/img/asterisco.svg);
                display: inline-block;
                width: 1rem;
                height: .8rem;
                content: "";
                background-position: center;
                background-repeat: no-repeat;
                padding-right: 1rem;
            }
        }
    }

}

.book-info {
    text-align: center;
    margin: 3rem 0;

    .cover {
        max-width: 10rem;
        border-radius: .2rem;
        box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 1px, rgba(0, 0, 0, 0.07) 0px 2px 2px, rgba(0, 0, 0, 0.07) 0px 4px 4px, rgba(0, 0, 0, 0.07) 0px 8px 8px, rgba(0, 0, 0, 0.07) 0px 16px 16px;
    }
}