#ritratto {
    float: right;
    margin-left: 2rem;
    width: 8rem;
    box-shadow: 0 0 10px 10px rgba(50, 50, 93, 0.25);
    
    img {
        border-image-source: url(/img/frame.png);
        border-image-slice: 100 100;
        border-image-outset: 4;
        // border-image-source: url(/img/frame2.png);
        // border-image-slice: 260 280;
        // border-image-outset: 9;
        border-image-repeat: round;
        border-image-width: 10;
        width: 8rem;
    }
}

@media only screen and (max-width: 500px) {
    #ritratto {
        float: none;
        margin: 2rem auto;
    }
}