#footer {
    color: $site_footer;
    font-size: .7rem;
    width: 100%;
    color: $dimmed_text;
    text-align: center;

    #altre-pagine {
        max-width: $max_width;
        margin: 0 auto;

        ul {
            display: flex;
            flex-direction: row;
            justify-content: space-evenly;
            padding: 0;
            flex-wrap: wrap;
            
            li {
                list-style: none;
                display: inline;
            }
        }
    }
    
    #impressum {
        padding-top: 1rem;
        margin-top: 1rem;
    }

    a {
        color: $site_footer;

        &:hover {
            color: $site_footer_hover_link;
        }
    }

    p {
        margin: 0;
    }
}